.CG2-circulation {
  @include max-screen( $breakpoint-middle ) {
    display: table;
    width: 100%;
  }
}
  .CG2-circulation__lastUpdate {
    font-family: $fontSerif;
    font-feature-settings: "palt";
    float: right;
    @include max-screen( $breakpoint-middle ) {
      float: none;
      display: table-footer-group;
    }
